body {
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

.person {
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
}

.person__photo {
  display: flex;
}

.person__img {
  border-radius: 50%;
  width: 100%;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-self: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  right: 10px;
}

.person__name {
  text-align: center;
  color: #2f4f4f;
  margin-top: 8px;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
  display: block;
}

.person__extra {
  display: none;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    padding: 30px 55px;
  }
}
